.Movie {
  background-color: #ffffff;
  width: 40%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 50px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding: 0 20px;
  -webkit-box-shadow: 0 8px 40px rgba(133, 133, 133, 0.3), 0 4px 12px rgba(133, 133, 133, 0.2);
          box-shadow: 0 8px 40px rgba(133, 133, 133, 0.3), 0 4px 12px rgba(133, 133, 133, 0.2)
}

.Movie__Column {
  width: 30%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.Movie__Column:last-child{
  padding: 20px 0;
  width: 60%;
}

.Movie h1 {
  font-size: 24px;
  font-weight: 600;
}

.Movie .Movie__Genres {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 20px;
}

.Movie__Genres .Movie__Genres {
  font-weight: 500;
  margin-right: 10px;
  color: #B4B5BD;
}

.Movie .Movie__Synopsis {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  color: #B4B4BD;
  overflow: hidden;
  font-size: 12px;
  line-height: 1.4;
}

.Movie .Movie__Poster {
  max-width: 100%;
  position: relative;
  top: -20px;
  -webkit-box-shadow: -8px 16px 32px rgba(83, 83, 83, 0.3), 8px 16px 12px rgba(80, 80, 80, 0.2);
          box-shadow: -8px 16px 32px rgba(83, 83, 83, 0.3), 8px 16px 12px rgba(80, 80, 80, 0.2)
}

@media screen and (min-width: 320px) and (max-width: 667px){
  .Movie {
    width: 100%;
  }
}

@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
  .Movie {
    width: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .Movie__Poster {
    top: 0;
    left: 0;
    width: 100%;
  }
  .Movie__Column {
    width: 100%!important;
  }
}



